// Base fonts
$helvetica-r:    'helvetica-r',    Arial,  sans-serif;
$helvetica-m:    'helvetica-m',    Arial,  sans-serif;
$helvetica-b:    'helvetica-b',    Arial,  sans-serif;
$helvetica-cr:    'helvetica-cr',    Arial,  sans-serif;
$helvetica-cm:    'helvetica-cm',    Arial,  sans-serif;
$helvetica-cb:    'helvetica-cb',    Arial,  sans-serif;


// Base colors
$black:           		    #000;
$black_light1: 						#0A0A0A;
$black_light2: 						#242D2C;
$black_light3: 						#20253D;
$black_light4: 						#333333;
$white:           		    #ffffff;
$grey:             		    #444444;
$pink:             		    #f33c7a;
$blue:            		    #1F71BC;
$yellow:            		  #FFFF00;
$orange:            		  #D0682E;
$bg_body:									#F8F8F9;
$umico_red:								#F70551;
$umico_purple:						#8969D3;
$data_color:              #A69E9E;
$light_grey:              #EDEDED;
$text_grey:               #808080;
$greyish:                 #D4D7DA;
$greyish1:                #E9E8E8;
$greyish2:                #F1F2F3;
$greyish3:                #808080;
$greyish4:                #B7B6C2;

$acceptor_green:     	    #77BC1F;
$acceptor_text_color:		  #273A4B;
$acceptor_articul:   		  #777777;
$acceptor_red:      		  #D60000;
$acceptor_blue:						#2D9CDB;


$vk_soc_color:						#4A76A8;
$yt_soc_color: 						#F8271A;
$inst_soc_color: 					#A962BB;
$fb_soc_color: 						#19AEFF;


// Base mixin
@mixin helvetica-r {
	font-family: $helvetica-r;
	font-weight: 400; }
@mixin helvetica-m {
	font-family: $helvetica-m;
	font-weight: 400; }
@mixin helvetica-b {
	font-family: $helvetica-b;
	font-weight: 400; }

@mixin helvetica-cr {
	font-family: $helvetica-cr;
	font-weight: 400; }
@mixin helvetica-cm {
	font-family: $helvetica-cm;
	font-weight: 400; }
@mixin helvetica-cb {
	font-family: $helvetica-cb;
	font-weight: 400; }


// helvetica small regular
@mixin text_small_cr {
	@include helvetica-cr;
	font-size: 14px;
	line-height: 1.5;
	letter-spacing: 0.03em; }

@mixin text_small_r {
	@include helvetica-cr;
	font-size: 14px;
	line-height: 1.5;
	letter-spacing: 0.03em; }

// helvetica small bold
@mixin text_small_cb {
	@include helvetica-cb;
	font-size: 14px;
	line-height: 1.5;
	letter-spacing: 0.03em; }

// helvetica small medium
@mixin text_small_cm {
	@include helvetica-cm;
