// box shadow
@mixin bx_shadow_cart {
	box-shadow: 0px 0px 15px rgba($black, 0.09); }

@mixin bx_shadow_slider_btn {
	box-shadow: 0px 0px 10px rgba($black, 0.15); }

@mixin bx_shadow_social {
	box-shadow: 0px 0px 10px rgba($black, 0.09); }

@mixin bx_shadow_cart_light {
	box-shadow: 0px 0px 20px rgba($black, 0.15); }

@mixin bx_shadow_cart_input {
	box-shadow: 0px 0px 20px rgba($black, 0.05); }

@mixin bx_shadow_social {
	box-shadow: 0px 0px 20px rgba($black, 0.09); }

@mixin bx_shadow_timeline_cart {
	box-shadow: 0px 0px 50px rgba($black, 0.15); }

@mixin bx_shadow_footer {
	box-shadow: 0px 0px 15px rgba($black, 0.09); }

@mixin bx_shadow_big {
	box-shadow: 0px 0px 50px rgba($black, 0.15); }

@mixin bx_shadow_subs {
	box-shadow: 0px 0px 9px rgba(115, 115, 115, 0.25); }

@mixin bx_shadow_tabs {
	box-shadow: 5px 0px 7px rgba($black, 0.15); }


// calc px to em
$context: 16px;
@function em($pixels, $context: 16px) {
	@return #{$pixels/$context}em; }


// general btn
@mixin general-btn {
	display: inline-block;
	padding: 11px 41px;
	@include text_high_cr;
	color: $white;
	transition: all 0.35s linear;
	border-radius: 5px; }




// background setting's
@mixin bg-setting {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center; }

// Transition snippet's
@mixin trs($all: all, $time: 0.3s, $effect: linear, $timeout: 0s) {
	transition: $all $time $effect $timeout; }

