.preloader {
  position: relative !important;
  z-index: 11 !important;
  background: #f0f0f0 !important;
  overflow: hidden !important;
  border: none !important;
  fill: rgba(255, 255, 255, 0) !important;
  stroke: rgba(255, 255, 255, 0) !important;
  color: rgba(255, 255, 255, 0) !important;
}

.preloader::placeholder {
  opacity: 0;
}

.preloader.preloader--o::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #fbfbfb;
}

.preloader.preloader--h::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #ffffff;
  transition: all 0.3s linear 0s;
}

.preloader.preloader--h::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 12;
  display: block;
  width: 100%;
  height: 20%;
  margin-top: -5%;
  transform: translateY(-500%);
  filter: blur(1px);
  opacity: 0.5;
  background: linear-gradient(120deg, #fdfbfb 20%, #ebedee 80%);
  animation: nextLineVertical 2s linear infinite;
  transition: all 0.3s linear 0s;
}

.preloader.preloader--v::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #ffffff;
  transition: all 0.3s linear 0s;
}

.preloader.preloader--v::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 12;
  display: block;
  width: 60%;
  height: 100%;
  margin-top: 0;
  transform: translateY(-200%);
  filter: blur(1px);
  background: linear-gradient(120deg, #fdfbfb 20%, #ebedee 80%);
  animation: nextLineHorizontal 2s linear infinite;
  transition: all 0.3s linear 0s;
}

.prealoder-pdf {
  position: relative;
  z-index: 1;
}

.prealoder-pdf::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 11;
  width: 100%;
  height: 100%;
  background: #F8F8F9;
  transition: all 0.3s linear 0s;
}

.prealoder-pdf::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 12;
  display: block;
  width: 60%;
  height: 100%;
  margin-top: 0;
  transform: translateY(-200%);
  filter: blur(1px);
  background: linear-gradient(120deg, #fdfbfb 20%, #ebedee 80%);
  animation: nextLineHorizontal 2s linear infinite;
  transition: all 0.3s linear 0s;
}

.preloader-after::after,
.preloader-after::before {
  display: none;
}

@keyframes nextLineVertical {
  0% {
    transform: translateY(-500%);
  }

  100% {
    transform: translateY(500%);
  }
}

@keyframes nextLineHorizontal {
  0% {
    transform: translateX(-200%);
  }

  100% {
    transform: translateX(200%);
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes flipInX {
  0% {
    transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
    transition-timing-function: ease-in;
    opacity: 0;
  }

  40% {
    transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
    transition-timing-function: ease-in;
  }

  60% {
    transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
    opacity: 1;
  }

  80% {
    transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
  }

  100% {
    transform: perspective(400px);
  }
}

@keyframes flipOutX {
  0% {
    transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
  }

  30% {
    transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
    opacity: 1;
  }

  100% {
    transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
    opacity: 0;
  }
}

@keyframes bounceOut {
  20% {
    transform: scale3d(0.9, 0.9, 0.9);
  }

  50%, 55% {
    transform: scale3d(1.1, 1.1, 1.1);
    opacity: 1;
  }

  100% {
    transform: scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }
}

@keyframes bounceIn {
  0% {
    transform: scale3d(0.3, 0.3, 0.3);
    opacity: 0;
  }

  20% {
    transform: scale3d(1.1, 1.1, 1.1);
  }

  40% {
    transform: scale3d(0.9, 0.9, 0.9);
    opacity: 1;
  }

  60% {
    transform: scale3d(1.03, 1.03, 1.03);
  }

  80% {
    transform: scale3d(0.97, 0.97, 0.97);
  }

  100% {
    transform: scale3d(1, 1, 1);
  }
}

