.careers-timeline-user__descr {
	padding: 20px;
	@include bx_shadow_timeline_cart; }

.careers-timeline-user__name {
	@include caption_small_cb;
	color: $acceptor_green; }

.careers-timeline-user__release-time {
	margin-bottom: 10px;
	@include text_middle_cb;
	color: $data_color; }

.careers-timeline-user__text {
	@include text_middle_r;
	color: $black_light1; }

.careers-timeline__line {
	// position: relative
	@include from(780px) {
		position: relative; } }

.careers-timeline__line-inner {
	width: 9px;
	min-width: 9px;
	// height: 100vh
	min-height: 100%;
	max-height: 100%;
	opacity: .3;
	background: linear-gradient(180deg, $white 0%, $data_color 3.38%);
	@include sm-block() {
		position: absolute;
		// top: 0
		// left: 0
		height:  9px;
		min-height:  9px;
		width: 100%; } }


.careers-timeline__load-btn {
	@include from(780px) {
		position: absolute;
		bottom: 0;
		z-index: 1;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		min-width: 124px;
		max-width: 124px;
		min-height: 124px;
		margin-left: -54px;
		margin-bottom: -124px;
		@include text_small_cb;
		border-radius: 50%;
		color: $white;
		background: $acceptor_green; }
	@include trs;
	&.careers-timeline__load-btn--mobile {
		display: none;
		@include sm-block() {
			display: block;
			margin: 0 auto;
			text-align: center; } }

	&.careers-timeline__load-btn--desktop {
		@include sm-block() {
			display: none; } }
	&:hover {
		background: $acceptor_blue; } }



@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
	.careers-timeline__load-btn {
		@include from(780px) {
			min-height: unset;
			height: 124px; } } }



// print
@media print {

	.careers-info__col-content {
		visibility: visible; }


	.careers-update,
	.header,
	.footer,
	.careers-info__col-aside,
	.careers-info__col,
	.careers-info__col-full,
	.benefits-slider-pagination,
